//imports 
import React, {useState} from 'react';
import { withTranslation } from 'react-i18next';

function AboutMe({t}) {

  const [visible, setVisible] = useState(true);

  const toggle = () => {
    setVisible(!visible);
  }

  
  return (


<div className="content">
        <div className="title space-between" >
            <h1>About me</h1>
            <i className="far fa-user fa-2x title-logo" />
        </div>
        <div className="body">
          <div className="space-between top">
          <div id="statement">
              <h2>{t('about.summary_subtitle')}</h2>
          <ul>
            {t('about.summary_bullets', { returnObjects: true }).map((bullet, i) => 
              <li key={i}>{bullet}</li>
            )}
              <ul>
                {t('about.summary_subbullets', { returnObjects: true }).map((bullet, i) => 
                  <li key={i}><strong>{bullet[0]}</strong>{bullet[1]}</li>
                )}
              </ul>
          </ul>

          </div>
          <div id="info">
            <h2>{t('about.info_subtitle')}</h2>
            <p><span className="bold">{t('about.name')}</span>Piotr Żakowicz</p>
            <p><span className="bold">{t('about.location_label')}</span>{t('about.location')}</p>
            <p><span className="bold">Email : </span>piotr.zakowicz@gmail.com</p>
            <div className="flex">
              <span className="bold">{t('about.contact')}</span>
              <a href="https://www.linkedin.com/in/piotr-%C5%BCakowicz-31550955/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin fa-2x"></i></a>
              <a href="mailto: piotr.zakowicz@gmail.com" target="_blank" rel="noopener noreferrer"><i className="fas fa-envelope fa-2x"></i></a>
            </div>
            {visible ? 
            <button id="download" className="blue-button" onClick={toggle}>{t('about.download')}</button>
            : <div className="flex">
              <a href="Piotr_Żakowicz_CV_PL.pdf" target="_blank" rel="noopener noreferrer"><button className="blue-button sub-button" onClick={toggle}>{t('about.pl')}</button></a>
              <a href="Piotr_Żakowicz_CV_ENG.pdf" target="_blank" rel="noopener noreferrer"><button className="blue-button sub-button" onClick={toggle}>{t('about.en')}</button></a>
            </div>}

          </div>
        </div>
          <h2>{t('about.obj_subtitle')}</h2>
          <h4 id="mission">{t('about.obj_1')}</h4>
          <p>{t('about.obj_2')}</p>

        </div>

    </div>
  )


}

export default withTranslation()(AboutMe)