import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import './translations/i18n';
import './index.css';
import NavBar from './components/NavBar/NavBar';
import AboutMe from './components/AboutMe/AboutMe';
// import Portfolio from './components/Portfolio/Portfolio';
import CV from './components/CV/CV';
import Lyftbros from './components/Portfolio/Lyftbros';
// import CVWebsite from './components/Portfolio/CVWebsite';

ReactDOM.render(
      <BrowserRouter>
        <Switch>
          <Route exact path="/">
            <NavBar active="1" />
            <AboutMe />
          </Route>
          <Route path="/cv">
            <NavBar active="2"/>
            <CV />
          </Route>
          <Route exact path="/portfolio">
            <NavBar active="3" />
            <Lyftbros />
          </Route>
          {/* <Route path="/portfolio/lyftbros">
            <NavBar active="3" />
            <Lyftbros />
          </Route>
          <Route path="/portfolio/cv_website">
            <NavBar active="3" />
            <CVWebsite />
          </Route> */}
        </Switch>
      </BrowserRouter>,
  document.getElementById('root')
);

