//imports 
import React, {useState} from 'react';
import { withTranslation } from 'react-i18next';
import { CSSTransition } from 'react-transition-group';

function Lyftbros({t}) {

	const [image, setImage] = useState(true);

	const changeImage = () => {
		setTimeout(setImage)
	}
  
  return (
      <div className="content">
            <div className="title space-between">
							<h1>Portfolio</h1>
							<i className="fa fa-github fa-2x title-logo" />
            </div>
            <div className="body">
							<div className="details-container">
											<img src="../iphone_frame.svg" alt="iphone feed" id="frame" />
											<CSSTransition
												in={image}
												timeout={1000} 
												classNames="slide3"
												unmountOnExit
											>
											<img src="../feed.png" alt="iphone feed" className="screen" />
											</CSSTransition>
								<div id="lbs-summary">
										<h2>Lyftbros</h2>
										<span>{t('lyftbros.summary')}</span>
										<div>
											<a href="https://evening-wildwood-46290.herokuapp.com/" target="_blank" rel="noopener noreferrer" className="link button-container2">
												<button className="blue-button lbs-button">Visit</button>
											</a>

											<a href="https://github.com/pzakowicz/lyftbros" target="_blank" rel="noopener noreferrer" className="link button-container2">
												<button className="blue-button lbs-button">Repository</button>
											</a>
										</div>
										<h2 className="heading">{t('lyftbros.features')}</h2>
										<ul>
											{t('lyftbros.feature_list', { returnObjects: true }).map((feature, i) => 
												<li key={i}>{feature}</li>
												)}
										</ul> 

								</div>
						
	
				
							
									<h2 className="heading">{t('lyftbros.tech')}</h2>
						
									<h3 className="subheading">Front-end</h3>
									<p>{t('lyftbros.front-summary')}</p>
									<ul>
										{t('lyftbros.front-bullets', { returnObjects: true }).map((bullet, i) => 
											<li key={i}>{bullet}</li>
											)}
									</ul>
									<h3 className="subheading">Back-end</h3>
									<p>{t('lyftbros.back-summary')}</p>
									<ul>
									{t('lyftbros.back-bullets', { returnObjects: true }).map((bullet, i) => 
										<li key={i}>{bullet}</li>
									)}
									</ul>
									<p className="subheading">{t('lyftbros.prev')}</p>
									<ul>
										{t('lyftbros.prev-bullets', { returnObjects: true }).map((bullet, i) => 
											<li key={i}>{bullet}</li>
										)}
									</ul>
							</div>
					

						
            </div>

      </div>
  )


}

export default withTranslation()(Lyftbros);