//imports 
import React from 'react';
import { withTranslation } from 'react-i18next';

function Skills({t}) {
  
  return (
    <div>
      <h3>{t('skills.software')}</h3>
      <div className="flex top">
        <div className="list">
          <p className="bold">Frontend:</p>
          <ul>
            <li>React (+ transition group)</li>
            <li>Redux (+ thunk, persist)</li>
            <li>React router</li>
            <li>Service worker + cache API + web push</li>
            <li>Localforage (IndexedDB)</li>
            <li>Webpack + Babel</li>
            <li>Font awesome</li>
          </ul>
        </div>
        <div className="list">
          <p className="bold">Backend:</p>
          <ul>
            <li>Node.js</li>
            <li>Express</li>
            <li>Embedded JS (EJS)</li>
            <li>Passport + Express-session + bcrypt</li>
            <li>Dotenv</li>
            <li>Nodemon</li>
            <li>Cors</li>
            <li>Morgan</li>
          </ul>
        </div>
        <div className="list">
          <p className="bold">{t('skills.database')}</p>
          <ul>
            <li>MySQL (Workbench)</li>
            <li>SQLite (DB Browser)</li>
          </ul>
        </div>
        <div className="list">
          <p className="bold">{t('skills.testing')}</p>
          <ul>
            <li>JEST</li>
            <li>Postman</li>
          </ul>
        </div>
        <div className="list">
          <p className="bold">{t('skills.design')}</p>
          <ul>
            <li>Enterprise Architect</li>
            <li>Inkscape</li>
          </ul>
        </div>
        <div className="list">
          <p className="bold">{t('skills.misc')}</p>
          <ul>
            <li>Visual Studio code</li>
            <li>Git + Github</li>
          </ul>
        </div>
      </div>
      <div className="skill-container">
              <h3>{t('skills.languages')}</h3>
              <ul>
                <li><span className="language">{t('skills.pl')}</span>{t('skills.pl_lvl')}</li>
                <li><span className="language">{t('skills.en')}</span>{t('skills.en_lvl')}</li>
                <li><span className="language">{t('skills.fr')}:</span>{t('skills.fr_lvl')}</li>
                <li><span className="language">{t('skills.ru')}</span>{t('skills.ru_lvl')}</li>
              </ul>
      </div>
    </div>

  )


}

export default withTranslation()(Skills);